import { Injectable } from '@angular/core';
import { QuoteFormComponent } from './quote-form/quote-form.component';
import { QuoteSamples } from './quotes/sample-quotes';


@Injectable({
  providedIn: 'root'
})
export class QuoteDetailsService {

  constructor(name: string, author: string, quote: string,
    upVotes: number,
    downVotes: number,
    timePublished) {
      name = '',
      author = '',
      quote = '',
      upVotes = 0,
      downVotes = 0,
      timePublished = new Date()
    }

}
